import { Component, Prop, h } from '@stencil/core';

@Component({
  tag: 'ir-button',
})
export class IrButton {
  @Prop({ reflect: true }) type: 'button' | 'submit' | 'reset' = 'button';
  @Prop({ reflect: true }) btnTitle: string = '';
  @Prop({ reflect: true }) icon: string = '';
  @Prop({ reflect: true }) variant: 'primary' | 'secondary' = 'primary';
  @Prop() shadow: boolean;
  @Prop() btnBlur: boolean;
  render() {
    return (
      <button type={this.type} class={`btn btn-${this.variant} ${this.shadow ? 'box-shadow-1' : ''}`}>
        <i class={this.icon}></i>
        {this.btnTitle}
      </button>
    );
  }
}
